.page {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.card__items {
  margin: 0 16px;
}

.card__items--logo {
  height: 134px;
  margin: 32px 19px 0;
}

.card__items--title {
  color: #060b35;
  margin-top: 40px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.card__items--code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.card__items--rating {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.card__items--rating .reviews {
  color: #060b35;
  text-align: right;
  justify-content: flex-end;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.card__items--price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__items--button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  margin: 16px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
}

.card__items--button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.price {
  margin-top: 0;
  margin-bottom: 0;
  font-style: normal;
  line-height: 18px;
}

.price--text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.price--number {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
}

.stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

